	import {  
        mapMutations  
    } from 'vuex';
	
	export default{
		data(){
			return {
				mobile: '',
				password: '',
				logining: false
			}
		},
		onLoad(){
			
		},
		methods: {
			...mapMutations(['login']),
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navBack(){
				uni.navigateBack();
			},
			toRegist(){
 				uni.navigateTo({
					url:"/pages/public/register"
				})
			},
			async toLogin(){
				this.logining = true;
				const {mobile, password} = this;
				/* 数据验证模块
				if(!this.$api.match({
					mobile,
					password
				})){
					this.logining = false;
					return;
				}
				*/ 
				var _this = this;
				this.sendRequest({
					url : "login",
					data : {username : mobile,password:password},
					method : "post",
 					success:function (res) {
 						if(res.code == '200'){
							//如果是app的登录
							_this.$api.msg(res.message);
							uni.setStorageSync(_this.sessionKey,res.data.token);
							_this.login(res.data);
							uni.navigateBack();  
						}else{
							_this.$api.msg(res.message);
							_this.logining = false;
						} 
					}
				});  
			}
		},

	}
